import React, { useState } from 'react';
import {
  Box,
  Typography,
  Container,
  List,
  ListItem,
  ListItemText,
  ListItemButton,
  Paper,
  IconButton,
  Chip,
  TextField,
  InputAdornment,
  Pagination,
  Alert,
  CircularProgress,
} from '@mui/material';
import {
  PlayArrow,
  Add,
  Search as SearchIcon,
  MusicNote,
} from '@mui/icons-material';
import { useSongs } from '../../hooks/useSongs';
import { Song } from '../../domains/songs/models';
import styles from './SongList.module.css';

const SongList: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const songsPerPage = 10;

  const { songs, loading, error, pagination, searchSongs } = useSongs({
    page: currentPage,
    pageSize: songsPerPage,
  });

  const handleSearch = (query: string) => {
    setSearchTerm(query);
    setCurrentPage(1);
    searchSongs(
      {
        query,
      },
      1,
      songsPerPage
    );
  };

  const handlePageChange = (_: React.ChangeEvent<unknown>, page: number) => {
    setCurrentPage(page);
    searchSongs(
      {
        query: searchTerm,
      },
      page,
      songsPerPage
    );
  };

  const handlePlay = (song: Song) => {
    console.log('Playing:', song.title);
    // In real app, this would start playing the song
  };

  const handleAddToPlaylist = (song: Song) => {
    console.log('Adding to playlist:', song.title);
    // In real app, this would add to current playlist
  };

  return (
    <Container maxWidth='lg' className={styles.container}>
      <Box sx={{ py: 4 }}>
        <Typography variant='h3' component='h1' gutterBottom>
           Song Library
        </Typography>
        <Typography
          variant='h6'
          component='p'
          color='text.secondary'
          sx={{ mb: 4 }}
        >
          Browse and manage your song collection
        </Typography>

        <Box sx={{ mb: 3 }}>
          <TextField
            fullWidth
            variant='outlined'
            placeholder='Search songs or artists...'
            value={searchTerm}
            onChange={e => handleSearch(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position='start'>
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
          />
        </Box>

        {error && (
          <Alert severity='error' sx={{ mb: 3 }}>
            {error}
          </Alert>
        )}

        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
            <CircularProgress />
          </Box>
        ) : (
          <>
            <Paper elevation={1}>
              <List>
                {songs.length === 0 ? (
                  <ListItem>
                    <ListItemText
                      primary='No songs found'
                      secondary='Try adjusting your search terms'
                      sx={{ textAlign: 'center', py: 4 }}
                    />
                  </ListItem>
                ) : (
                  songs.map(song => (
                    <ListItem key={song.id} divider>
                      <ListItemButton
                        onClick={() => handlePlay(song)}
                        sx={{ flex: 1 }}
                      >
                        <MusicNote sx={{ mr: 2, color: 'primary.main' }} />
                        <ListItemText
                          primary={song.title}
                          secondary={`${song.artist}  ${song.formattedDuration}`}
                        />
                        <Chip
                          label={song.genre}
                          size='small'
                          color='primary'
                          variant='outlined'
                          sx={{ mr: 2 }}
                        />
                      </ListItemButton>
                      <IconButton
                        onClick={() => handlePlay(song)}
                        color='primary'
                        aria-label='play'
                      >
                        <PlayArrow />
                      </IconButton>
                      <IconButton
                        onClick={() => handleAddToPlaylist(song)}
                        color='secondary'
                        aria-label='add to playlist'
                      >
                        <Add />
                      </IconButton>
                    </ListItem>
                  ))
                )}
              </List>
            </Paper>

            {pagination.totalPages > 1 && (
              <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
                <Pagination
                  count={pagination.totalPages}
                  page={currentPage}
                  onChange={handlePageChange}
                  color='primary'
                />
              </Box>
            )}
          </>
        )}
      </Box>
    </Container>
  );
};

export default SongList;
